html {
  font-size: 16px; }

body {
  font-family: 'Raleway', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

nav {
  height: 0; }

p {
  margin: 0; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }
  @media (max-width: 1024px) {
    .wrap {
      min-width: 320px;
      max-width: 800px;
      padding: 0 15px;
      box-sizing: border-box;
      margin: 0 auto; } }
  @media (max-width: 768px) {
    .wrap {
      min-width: 320px;
      max-width: 748px;
      padding: 0 15px;
      box-sizing: border-box;
      margin: 0 auto; } }
  @media (max-width: 320px) {
    .wrap {
      min-width: 320px;
      max-width: 310px;
      padding: 0 15px;
      box-sizing: border-box;
      margin: 0 auto; } }

.works {
  background-color: #ffffff;
  padding: 145px 0 130px 0; }
  .works .works__title {
    color: #454750;
    font-weight: 500;
    font-size: 43px;
    letter-spacing: 0.02em;
    position: relative;
    margin-bottom: 50px; }
    @media (max-width: 320px) {
      .works .works__title {
        font-size: 28px; } }
    .works .works__title:before {
      position: absolute;
      content: "";
      height: 2px;
      width: 50px;
      background-color: #3685EE;
      top: -15px; }
  .works .works__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .works .works__element {
    flex-basis: 31.623%;
    margin-bottom: 30px; }
    @media (max-width: 1024px) {
      .works .works__element {
        flex-basis: 48.051%; } }
    @media (max-width: 768px) {
      .works .works__element {
        flex-basis: 48.050%; } }
    @media (max-width: 320px) {
      .works .works__element {
        flex-basis: 100%; } }
  .works .works__link {
    display: block;
    height: 100%;
    text-decoration: none;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    box-sizing: border-box;
    position: relative; }
    .works .works__link:before {
      transition-property: opacity;
      transition-duration: 500ms;
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: #3685EE;
      opacity: 0; }
    .works .works__link:hover:before {
      opacity: 1; }
    .works .works__link:hover .works__description {
      color: #3685EE; }
    .works .works__link:hover .works__imgBlock:after {
      opacity: 1; }
    .works .works__link:hover .works__imgBlock:before {
      opacity: 1; }
    .works .works__link--active .works__description {
      color: #3685EE; }
    .works .works__link--active .works__imgBlock:after {
      opacity: 1; }
    .works .works__link--active .works__imgBlock:before {
      opacity: 1; }
  .works .works__description {
    font-size: 16px;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: 0;
    text-align: left;
    padding: 15px 30px 21px 30px;
    color: #454750;
    transition-property: color;
    transition-duration: 500ms; }
    @media (max-width: 768px) {
      .works .works__description {
        padding: 12px 15px 17px 30px; } }
    @media (max-width: 400px) {
      .works .works__description {
        padding: 12px 15px 17px 15px;
        font-size: 14px; } }
    @media (max-width: 320px) {
      .works .works__description {
        padding: 9px 25px 14px 25px; } }
  .works .works__imgBlock {
    position: relative; }
    .works .works__imgBlock:after {
      transition-property: opacity;
      transition-duration: 500ms;
      content: url(../img/enlarge.svg);
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
      width: 26px;
      height: 26px;
      opacity: 0; }
    .works .works__imgBlock:before {
      transition-property: opacity;
      transition-duration: 500ms;
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: #ffffff;
      opacity: 0; }
  .works .works__photo {
    border-radius: 2px;
    width: 100%;
    height: auto;
    max-height: 273px; }
